define('qumla/helpers/add-num', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports.add = add;

	function add(params) {
		var num = 0;
		for (var i = 0; i < params.length; i++) {
			num += params[i];
		}
		return num;
	}

	exports['default'] = Ember['default'].Helper.helper(add);

});