define('qumla/components/pl-uploader', ['exports', 'ember-plupload/components/pl-uploader', 'qumla/config/environment'], function (exports, PlUploader, config) {

  'use strict';

  var BASE_URL = config['default'].PLUPLOAD_BASE_URL;

  if (BASE_URL == null) {
    if (config['default'].baseURL.slice(-1) === '/') {
      BASE_URL = config['default'].baseURL + 'assets/';
    } else {
      BASE_URL = config['default'].baseURL + '/assets/';
    }
  }

  exports['default'] = PlUploader['default'].extend({
    BASE_URL: BASE_URL
  });

});