define('qumla/pods/profile/forgotpwd/controller', ['exports', 'ember', 'ember-cp-validations', 'qumla/app'], function (exports, Ember, ember_cp_validations, App) {

	'use strict';

	var Validations = ember_cp_validations.buildValidations({
		email: [ember_cp_validations.validator('presence', { presence: true, message: ' ' }), ember_cp_validations.validator('format', { type: 'email' })]
	});

	exports['default'] = Ember['default'].Controller.extend(Validations, {
		email: null,
		session: Ember['default'].inject.service(),
		modal: Ember['default'].inject.service(),

		actions: {
			sendResetPasswordEmail: function sendResetPasswordEmail() {
				var reset = App['default'].getData('/forgot', true, 'POST', true, false, {
					email: this.get('email'),
					l: this.get('session.language')
				}, null, null);
				var c = this;
				this.get('loader').startLoadProcess(reset);
				reset.then(function () {
					c.get('modal').openInfoModal({ header: 'Password change request sent', text: App['default'].locX('/forgot/requestsent') });
					c.transitionToRoute('profile.index');
				}, function () {
					c.get('modal').openInfoModal({ header: 'Password change request error', text: App['default'].locX('/forgot/requestsent_error') });
				});
			}
		}
	});

});