define('qumla/helpers/get-max', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.getMax = getMax;

  function getMax(collection, field) {
    var max = null;
    collection.forEach(function (stat) {
      if (max === null) {
        max = stat;
        return;
      }
      var result = stat.get(field);
      if (parseInt(result) > parseInt(max.get(field))) {
        max = stat;
      }
    });
    return max;
  }

  exports['default'] = Ember['default'].Helper.helper(getMax);

});