define('qumla/adapters/language', ['exports', 'ember-data', 'qumla/app', 'ember'], function (exports, DS, App, Ember) {

	'use strict';

	exports['default'] = DS['default'].Adapter.extend({
		findAll: function findAll(store, type, sinceToken) {
			var url = type;
			var query = { since: sinceToken };
			return new Ember['default'].RSVP.Promise(function (resolve, reject) {

				Ember['default'].$.getJSON("/api/common/langlist").then(function (langList) {
					var i = 0;
					var data = [];
					for (var key in langList) {
						data.push({
							id: i++,
							type: 'language',
							attributes: {
								code: key,
								pcode: langList[key],
								name: App['default'].locX('/language/' + key)
							}
						});
					}
					Ember['default'].run(null, resolve, { data: data });
				}, function (jqXHR) {
					jqXHR.then = null; // tame jQuery's ill mannered promises
					Ember['default'].run(null, reject, jqXHR);
				});
			});
		}
	});

});