define('qumla/pods/profile/route', ['exports', 'ember', 'qumla/app'], function (exports, Ember, App) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend({
		session: Ember['default'].inject.service(),
		model: function model() {
			var pageS = App['default'].getData('/profile/setup', true, "GET", true, false, null, null, null);
			var userPromise = undefined;
			if (this.get('session.userid')) {
				userPromise = this.store.findRecord('user', this.get('session.userid'));
			}
			return Ember['default'].RSVP.hash({
				pageSetup: pageS,
				user: userPromise
			});
		},

		setupController: function setupController(controller, model) {
			controller.set('setup', model.pageSetup);
			this.get('session').set('user', model.user);
		}
	});

});