define('qumla/models/useracc', ['exports', 'ember', 'ember-data'], function (exports, Ember, DS) {

	'use strict';

	var User = DS['default'].Model.extend({
		name: DS['default'].attr('string'),
		login: DS['default'].attr('string'),
		email: DS['default'].attr('string'),
		hash: DS['default'].attr('string'),
		image: DS['default'].attr('boolean'),
		imagec: DS['default'].attr('number'),
		displayName: Ember['default'].computed('name', 'login', function () {
			if (this.get('login')) {
				return '@' + this.get('login');
			} else {
				return this.get('name');
			}
		})
	});
	Ember['default'].Inflector.inflector.uncountable('useracc');
	exports['default'] = User;

});