define('qumla/models/answerstat', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		childs: [],
		option: DS['default'].belongsTo('option'),
		question: DS['default'].belongsTo('question'),
		location: DS['default'].belongsTo('location', { async: false }),
		country: DS['default'].belongsTo('country', { async: false }),
		hour: DS['default'].attr('number'),
		answerdate: DS['default'].attr('datetime'),
		count: DS['default'].attr('number'),
		percent: DS['default'].attr('number'),
		total: DS['default'].attr('number'),
		color: DS['default'].attr('string'),
		copy: function copy() {
			var c = new Ember['default'].Object();
			c.set('option', this.get('option'));
			c.set('question', this.get('question'));
			c.set('location', this.get('location'));
			c.set('hour', this.get('hour'));
			c.set('answerdate', this.get('answerdate'));
			c.set('total', this.get('total'));
			c.set('percent', this.get('percent'));
			c.set('count', this.get('count'));
			c.set('country', this.get('country'));
			c.set('color', this.get('color'));
			c.set('childs', []);
			return c;
		}
	});
	Ember['default'].Inflector.inflector.uncountable('answerstat');
	exports['default'] = model;

});