define('qumla/pods/components/q-comment/component', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		tagName: '',
		postResponse: false,
		comment: null,
		actions: {

			togglePostResponse: function togglePostResponse() {
				this.toggleProperty('postResponse');
			},
			onComment: function onComment(question, parentComment, commentText) {
				this.sendAction('onComment', question, parentComment, commentText);
			}
		}
	});

});