define('qumla/instance-initializers/session', ['exports', 'ember', 'qumla/app'], function (exports, Ember, App) {

  'use strict';

  exports.initialize = initialize;

  function initialize(instance) {
    var store = instance.container.lookup('service:store');
    var modal = instance.container.lookup('service:modal');

    App['default'].session = instance.container.lookup('service:session');
    store.findAll('session').then(function (sessioRecords) {
      if (sessioRecords.get('length') === 0) {
        // request new session
        App['default'].getData('/token', false, 'POST', true, false, {}, function (data) {
          if (data.access_token) {

            var sessionInfo = store.createRecord('session', {});
            App['default'].session.updateSessionModel(data, sessionInfo);

            sessionInfo.save().then(function () /*status*/{

              App['default'].session.setup(sessionInfo);
              Ember['default'].run.later(function () {
                new Fingerprint2().get(function (fingerprint) {
                  App['default'].getData('/ping', true, 'POST', false, false, { 1: fingerprint }, function (fp) {
                    // update session
                    var oldToken = sessionInfo.get('token');
                    if (oldToken !== fp.access_token) {
                      modal.openInfoModal({ header: App['default'].locX('/token/old_found_header'), text: App['default'].locX('/token/old_found_text'),
                        action: function action() {
                          App['default'].session.updateSessionModel(fp).then(function () {
                            App['default'].reload();
                          });
                        } });
                    }
                  });
                });
              }, 2000);
            });
          }
        });
      } else {
        App['default'].session.setup(sessioRecords.get('firstObject'));
        App['default'].getData('/ping', true, 'POST', false, false, {}, function (data) {
          if (data !== "OK") {
            window.localStorage.clear();
            App['default'].reload();
          }
        }, function (status) {
          if (parseInt(status.status) === 403) {
            // token not found or expired
            Ember['default'].Logger.debug('token expired request new');
            window.localStorage.clear();
            App['default'].reload();
          }
        });
      }
    });
  }

  exports['default'] = {
    name: 'create-session',
    after: 'ember-data',
    initialize: initialize
  };

});