define('qumla/models/subscription', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		email: DS['default'].attr('string'),
		question: DS['default'].attr('number'),
		category: DS['default'].attr('string')
	});
	Ember['default'].Inflector.inflector.uncountable('subscription');

	exports['default'] = model;

});