define('qumla/pods/components/q-input/component', ['exports', 'qumla/components/one-way-input', 'ember'], function (exports, OneWayInput, Ember) {

	'use strict';

	exports['default'] = OneWayInput['default'].extend({
		classNameBindings: ['isValid:valid', 'isInvalid:invalid'],
		isInvalid: Ember['default'].computed('changed', 'isValid', function () {
			return this.get('changed') && !this.get('isValid');
		}),
		isValid: false,
		changed: false,
		_handleChangeEvent: function _handleChangeEvent() {
			this._super();
			Ember['default'].Logger.info('input changed: isValid' + this.get('isValid') + ' changed:' + this.get('changed'));
			this.set('changed', true);
		}
	});

});