define('qumla/pods/application/route', ['exports', 'ember', 'qumla/app', 'qumla/mixins/question-actions'], function (exports, Ember, App, QuestionActions) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend(QuestionActions['default'], {
    title: function title(tokens) {
      var title = '';
      if (!tokens.length || tokens.length === 0) {
        title = App['default'].locX('/title/application');
      } else {
        title = tokens.join(' - ');
      }
      Ember['default'].$("[data-og-title]").attr("content", title);
      return title;
    },
    actions: {
      loading: function loading(transition /*, originRoute*/) {
        this.get('loader').startLoadProcess(transition.promise);
        return true;
      },
      jumpToTag: function jumpToTag(tag) {
        if (tag.indexOf("#") === 0) {
          tag = tag.substring(1);
        }
        this.transitionTo("tag", tag);
      },
      jumpToQuestion: function jumpToQuestion(title, id) {
        this.transitionTo("question.detail", id, title);
      },
      jumpToSearch: function jumpToSearch(query) {
        this.transitionTo("search", query);
      },
      didTransition: function didTransition() {
        var _this = this;

        var router = this.get('router');
        var self = this;
        Ember['default'].run.schedule('afterRender', this, function () {
          Ember['default'].run.later(_this, function () {
            if (addthis && addthis.layers && addthis.layers.refresh) {
              addthis.layers.refresh();
            }
            if (router.get('url') === '/question/0/new/edit' || router.get('url').indexOf('/admin/') > -1) {
              Ember['default'].$('div#at4-share').attr('style', 'display:none !important');
              Ember['default'].$('div#at-share-dock').attr('style', 'display:none !important');
            } else {
              Ember['default'].$('div#at4-share').attr('style', 'display:block !important');
              Ember['default'].$('div#at-share-dock').attr('style', 'display:block !important');
            }
            if (router.get('url').indexOf('/question') == -1) {
              Ember['default'].$("[data-og-url]").attr("content", 'https://qumla.com' + router.get('url'));
              Ember['default'].$("[data-og-image-url]").attr("content", "https://qumla.com/images/share-icon.png");
              Ember['default'].$("[data-og-image]").attr("content", "https://qumla.com/images/share-icon.png");
              Ember['default'].$("[data-twitter-image]").attr("content", "https://qumla.com/images/share-icon.png");
              Ember['default'].$("[data-og-image-secure-url]").attr("content", "https://qumla.com/images/share-icon.png");
              Ember['default'].$("[data-og-description]").attr("content", "Ask your question! See others' opinion and give voice to yours.");
            }
          }, 1000);
        });
      }
    }
  });

});