define('qumla/components/winner-box', ['exports', 'ember', 'qumla/helpers/get-max'], function (exports, Ember, get_max) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		stats: null, //this is a answer-stat list
		winner: Ember['default'].computed('stats', function () {
			return get_max.getMax(this.get('stats'), 'count');
		})
	});

});