define('qumla/models/location', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		countryCode: DS['default'].attr('string'),
		name: DS['default'].attr('string'),
		lon: DS['default'].attr('number'),
		lat: DS['default'].attr('number')
	});
	Ember['default'].Inflector.inflector.uncountable('location');

	exports['default'] = model;

});