define('qumla/initializers/setup-store', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	exports.initialize = initialize;

	var TimeTransform = DS['default'].Transform.extend({
		deserialize: function deserialize(serialized) {
			return serialized;
		},
		serialize: function serialize(deserialized) {
			return deserialized;
		}
	});
	var DatetimeTransform = DS['default'].Transform.extend({
		deserialize: function deserialize(serialized) {
			if (Ember['default'].isEmpty(serialized)) {
				return;
			}
			Ember['default'].assert("Value type is not string", !!serialized.indexOf);
			return moment(serialized, 'YYYY-MM-DDTHH:mm:ss.SSSZZ');
		},
		serialize: function serialize(deserialized) {
			if (Ember['default'].isEmpty(deserialized)) {
				return;
			}
			Ember['default'].assert("Value type is not moment", !!deserialized.format);
			return deserialized.format('YYYY-MM-DDTHH:mm:ss.SSSZZ');
		}
	});
	var DateTransform = DS['default'].Transform.extend({
		deserialize: function deserialize(serialized) {
			if (Ember['default'].isEmpty(serialized)) {
				return;
			}
			Ember['default'].assert("Value type is not string", !!serialized.indexOf);
			return moment(serialized, 'YYYY-MM-DD');
		},
		serialize: function serialize(deserialized) {
			if (Ember['default'].isEmpty(deserialized)) {
				return;
			}
			Ember['default'].assert("Value type is not moment", !!deserialized.format);
			return deserialized.format('YYYY-MM-DD');
		}
	});
	var TimestampTransform = DS['default'].Transform.extend({
		deserialize: function deserialize(serialized) {
			if (Ember['default'].isEmpty(serialized)) {
				return;
			}
			Ember['default'].assert("Value type is not integer", !isNaN(serialized));
			return moment(serialized);
		},
		serialize: function serialize(deserialized) {
			if (Ember['default'].isEmpty(deserialized)) {
				return;
			}
			Ember['default'].assert("Value type is not moment", !!deserialized.format);
			return deserialized.format('YYYY-MM-DDTHH:mm:ss.SSSZZ');
		}
	});

	var RawTransform = DS['default'].Transform.extend({
		deserialize: function deserialize(serialized) {
			return serialized;
		},
		serialize: function serialize(deserialized) {
			return deserialized;
		}
	});

	function initialize(container /*, application*/) {
		container.register('transform:time', TimeTransform);
		container.register('transform:datetime', DatetimeTransform);
		container.register('transform:mdate', DateTransform);
		container.register('transform:timestamp', TimestampTransform);
		container.register('transform:raw', RawTransform);
	}

	exports['default'] = {
		after: 'store',
		name: 'setup-store',
		initialize: initialize
	};

});