define('qumla/pods/components/q-uploader/component', ['exports', 'ember', 'ember-plupload/components/pl-uploader'], function (exports, Ember, Pluploader) {

	'use strict';

	var _get = Ember['default'].get;
	var set = Ember['default'].set;
	var keys = Object.keys;

	exports['default'] = Pluploader['default'].extend({
		attachUploader: function attachUploader() {
			var config = _get(this, 'config');
			config.resize = {
				width: 650,
				height: 300,
				quality: 87
			};
			var uploader = _get(this, 'uploader');
			var queue = uploader.findOrCreate(_get(this, 'name'), this, config);
			set(this, 'queue', queue);
			// Send up the pluploader object so the app implementing this component as has access to it
			var pluploader = queue.get('queues.firstObject');
			this.sendAction('onInitOfUploader', pluploader);
			this._dragInProgress = false;
			this._invalidateDragData();
		},
		didInsertElement: function didInsertElement() {
			Ember['default'].run.scheduleOnce('afterRender', this, 'attachUploader');
			Ember['default'].run.scheduleOnce('afterRender', this, 'setupDragListeners');
		}
	});

});