define('qumla/pods/application/controller', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Controller.extend({
        loading: Ember['default'].computed.readOnly('loader.loading'),
        appstate: Ember['default'].inject.service(),
        session: Ember['default'].inject.service(),
        avatarUrl: Ember['default'].computed.readOnly('session.avatarUrl'),
        username: Ember['default'].computed.readOnly('session.username'),
        modal: Ember['default'].inject.service(),

        showModal: Ember['default'].computed.alias('modal.showModal'),
        modalName: Ember['default'].computed.readOnly('modal.modalName'),
        modalModel: Ember['default'].computed.readOnly('modal.modalModel'),

        actions: {
            closeModal: function closeModal() {
                this.set('showModal', false);
            }
        }
    });

});