define('qumla/models/language', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		code: DS['default'].attr('string'),
		pcode: DS['default'].attr('string'),
		name: DS['default'].attr('string')
	});

	Ember['default'].Inflector.inflector.uncountable('language');
	exports['default'] = model;

});