define('qumla/models/comment', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		question: DS['default'].belongsTo('question', { async: true }),
		original: DS['default'].belongsTo('comment', { inverse: 'replies' }),
		replies: DS['default'].hasMany('comment', { inverse: 'original' }),
		comment: DS['default'].attr('string'),
		session: DS['default'].attr('string'),
		useracc: DS['default'].belongsTo('useracc'),
		createdt: DS['default'].attr('timestamp'),
		postdate: Ember['default'].computed('createdt', function () {
			var d = this.get('createdt');
			if (d) {
				return d.fromNow();
			}
			return '';
		}),
		formatedComment: Ember['default'].computed('comment', function () {
			var c = this.get('comment');
			return c;
		}),
		avatarUrl: Ember['default'].computed('comment', 'useracc', 'session', function () {
			if (this.get('useracc.image')) {
				return "/api/profileimage?u=" + this.get('useracc.id') + "&c=" + this.get('useracc.imagec');
			}
			return "https://secure.gravatar.com/avatar/" + this.get('session') + "?s=50&d=wavatar";
		})

	});
	Ember['default'].Inflector.inflector.uncountable('comment');

	exports['default'] = model;

});