define('qumla/models/user', ['exports', 'ember', 'ember-data', 'qumla/app', 'ember-cp-validations'], function (exports, Ember, DS, App, ember_cp_validations) {

  'use strict';

  var Validations = ember_cp_validations.buildValidations({
    name: [ember_cp_validations.validator('presence', { presence: true, message: ' ' }), ember_cp_validations.validator('no-whitespace-around', true), ember_cp_validations.validator('length', { min: 4, max: 255 })],
    login: [ember_cp_validations.validator('unique-login', { debounce: 500 }), ember_cp_validations.validator('format', { regex: /^[a-zA-Z0-9]{4,32}$/, message: App['default'].locX('/signup/nickname_error'), allowBlank: true })],
    email: [ember_cp_validations.validator('presence', { presence: true, message: ' ' }), ember_cp_validations.validator('unique-email', { debounce: 500 }), ember_cp_validations.validator('format', { type: 'email' })],
    password: [ember_cp_validations.validator('presence', true), ember_cp_validations.validator('length', { min: 6, max: 16 })],
    password2: [ember_cp_validations.validator('presence', { presence: true, message: ' ' }), ember_cp_validations.validator('confirmation', {
      on: 'password',
      description: 'Passwords'
    })]
  });
  var User = DS['default'].Model.extend(Validations, {
    name: DS['default'].attr('string'),
    login: DS['default'].attr('string'),
    hash: DS['default'].attr('string'),
    email: DS['default'].attr('string'),
    password: DS['default'].attr('string'),
    password2: DS['default'].attr('string')
  });
  Ember['default'].Inflector.inflector.uncountable('user');
  exports['default'] = User;

});