define('qumla/models/option', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		answer: null,
		text: DS['default'].attr('string'),
		question: DS['default'].belongsTo('question'),
		style: DS['default'].attr('string'),
		ord: DS['default'].attr('number'),
		color: DS['default'].attr('string'),
		index: DS['default'].attr('number'),
		isSelected: Ember['default'].computed('answer', function () {
			return this.get('answer') === this.get('id');
		})

	});
	Ember['default'].Inflector.inflector.uncountable('option');

	exports['default'] = model;

});