define('qumla/models/tag', ['exports', 'ember', 'ember-data'], function (exports, Ember, DS) {

	'use strict';

	var TagModel = DS['default'].Model.extend({
		tag: DS['default'].attr('string'),
		type: DS['default'].attr('number'),
		count: DS['default'].attr('number')
	});
	Ember['default'].Inflector.inflector.uncountable('tag');
	exports['default'] = TagModel;

});