define('qumla/initializers/language', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.initialize = initialize;

  function initialize(container, application) {
    application.deferReadiness();
    Ember['default'].$.ajaxSetup({ async: false });
    Ember['default'].$.getJSON("/api/common/langtext", function (langtext) {
      for (var i = 0; i < langtext.length; i++) {
        var item = langtext[i];
        var c = '/' + item['type'] + '/' + item['code'] + '/' + item['language'];
        Ember['default'].STRINGS[c] = item['text'];
      }
    });
    Ember['default'].Logger.info('INIT Language DONE');
    Ember['default'].$.ajaxSetup({ async: true });
    application.advanceReadiness();
  }

  exports['default'] = {
    before: 'store',
    name: 'language-init',
    initialize: initialize
  };

});