define('qumla/models/answer', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		option: DS['default'].attr('number'),
		question: DS['default'].attr('number')
	});
	Ember['default'].Inflector.inflector.uncountable('answer');
	model.reopenClass({
		FIXTURES: [{
			id: 1,
			question: 2,
			option: 3,
			city: 1,
			hour: 2,
			count: 10
		}, {
			id: 2,
			option: 4,
			question: 2,
			city: 1,
			hour: 2,
			count: 13
		}, {
			id: 3,
			option: 5,
			question: 2,
			city: 1,
			hour: 2,
			count: 20
		}]
	});
	exports['default'] = model;

});