define('qumla/components/page-view', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		scrollToTop: false,
		tagName: "",
		didInsertElement: function didInsertElement() {
			if (this.get('scrollToTop')) {
				Ember['default'].run('afterRender', function () {
					Ember['default'].run.later(function () {
						Ember['default'].$("body").velocity("scroll", { duration: 1000, easing: "ease" });
					}, 200);
				});
			}
		}
	});

});