define('qumla/pods/question/route', ['exports', 'ember', 'qumla/mixins/geolocation-aware', 'qumla/app'], function (exports, Ember, GeoLocationAware, App) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend(GeoLocationAware['default'], {
		queryParams: {
			t: {
				refreshModel: true
			}
		},
		titleToken: 'Question detail',
		model: function model(params, transition) {
			var location = App['default'].getData('/location', false, 'GET', true, false, {});
			var question = params.question_id > 0 ? this.store.findRecord('question', params.question_id, { reload: true }) : this.store.createRecord('question', {});
			//let geolocation = this.getGeoLocation();
			return Ember['default'].RSVP.hash({
				question: question,
				location: location,
				geolocation: null
			});
		},
		setupController: function setupController(controller, model) {
			this.set('titleToken', model.question.get('title'));

			Ember['default'].$("[data-og-url]").attr("content", 'https://qumla.com/' + this.get('router').get('url'));
			Ember['default'].$("[data-og-title]").attr("content", model.question.get('title'));
			Ember['default'].$("[data-og-image-url]").attr("content", "https://qumla.com/img/question/" + model.question.get('img'));
			Ember['default'].$("[data-og-image]").attr("content", "https://qumla.com/img/question/" + model.question.get('img'));
			Ember['default'].$("[data-twitter-image]").attr("content", "https://qumla.com/img/question/" + model.question.get('img'));
			Ember['default'].$("[data-og-image-secure-url]").attr("content", "https://qumla.com/img/question/" + model.question.get('img'));
			Ember['default'].$("[data-og-description]").attr("content", model.question.get('description'));
			controller.set('question', model.question);
			controller.set('location', model.location);
			controller.set('geolocation', model.geolocation);
		}
	});

});