define('qumla/pods/hashtag/route', ['exports', 'ember', 'qumla/util/q-infinity-route', 'qumla/config/environment'], function (exports, Ember, QInfinityRoute, ENV) {

	'use strict';

	exports['default'] = QInfinityRoute['default'].extend({
		titleToken: 'Hashtag ',

		model: function model(params) {
			var questionPromise;
			if (params.tag) {
				questionPromise = this.infinityModel("question", { perPage: ENV['default'].APP.perPage, startingPage: 1, filter: { tag: params.tag } });
			}
			var tags = this.store.query('tag', { filter: { popular: true } });
			return Ember['default'].RSVP.hash({
				questions: questionPromise,
				tags: tags
			});
		},
		setupController: function setupController(controller, model) {
			this.set('titleToken', 'Hashtag - ' + this.paramsFor('hashtag').tag);
			controller.set('questions', model.questions);
			controller.set('tags', model.tags);
			controller.set('tagName', this.paramsFor('hashtag').tag);
		}
	});

});