define('qumla/mixins/comment-actions', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Mixin.create({
		actions: {
			onComment: function onComment(question, parent, text) {
				if (text === null || text.trim().length === 0) {
					return;
				}
				Ember['default'].debug('on comment called, send comment to server');
				var comment = this.store.createRecord('comment', { question: question, comment: text, original: parent });
				var self = this;
				comment.save().then(function () {
					Ember['default'].debug('comment saved');
					self.send('reloadHandler', 'comments', this);
				});
			}
		}
	});

});