define('qumla/pods/search/route', ['exports', 'ember', 'qumla/util/q-infinity-route', 'qumla/config/environment'], function (exports, Ember, QInfinityRoute, ENV) {

	'use strict';

	exports['default'] = QInfinityRoute['default'].extend({
		model: function model(params, transition) {
			var questionPromise = null;
			if (params.querystring && params.querystring.length > 0) {
				questionPromise = this.infinityModel("question", { perPage: ENV['default'].APP.perPage, startingPage: 1, filter: { query: params.querystring } });
			}
			return Ember['default'].RSVP.hash({
				questions: questionPromise,
				querystring: params.querystring
			});
		},
		setupController: function setupController(controller, model) {
			controller.set('questions', model.questions);
			controller.set('querystring', model.querystring);
		}
	});

});