define('qumla/pods/profile/forgotpwchange/controller', ['exports', 'ember', 'qumla/app'], function (exports, Ember, App) {

    'use strict';

    exports['default'] = Ember['default'].Controller.extend({
        session: Ember['default'].inject.service(),
        modal: Ember['default'].inject.service(),
        requestid: '',
        actions: {
            forgotPassword: function forgotPassword(email, newpPassword) {
                var reset = App['default'].getData('/forgotchange', true, 'POST', true, false, {
                    email: email,
                    new_password: newpPassword,
                    requestid: this.get('requestid')
                }, null, null);

                var self = this;

                reset.then(function () {
                    self.get('modal').openInfoModal({ header: App['default'].locX('/profile/password_changed_header'), text: App['default'].locX('/profile/password_changed'), action: function action() {
                            self.transitionToRoute('profile.index');
                        } });
                }, function (cause) {
                    self.get('modal').openInfoModal({ header: App['default'].locX('/profile/password_change_error'), text: '' });
                });
                return reset;
            }
        }
    });

});