define('qumla/pods/components/post-comment/component', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		tagName: '',
		question: null,
		commentText: '',
		parent: null,
		textareaid: Ember['default'].computed('parent', function () {
			var parent = this.get('parent');
			if (!parent) {
				return 'comment_area';
			} else {
				return 'comment_area_' + parent.get('id');
			}
		}),
		actions: {
			createComment: function createComment(question, parentComment, commentText) {
				this.set('commentText', '');
				this.sendAction('onComment', question, parentComment, commentText);
			}
		}
	});

});