define('qumla/models/session', ['exports', 'ember', 'ember-data'], function (exports, Ember, DS) {

	'use strict';

	var SessionModel = DS['default'].Model.extend({
		token: DS['default'].attr('string'),
		old_token: DS['default'].attr('string'),
		scope: DS['default'].attr('string'),
		username: DS['default'].attr('string'),
		login: DS['default'].attr('string'),
		userid: DS['default'].attr('string'),
		image: DS['default'].attr('boolean'),
		createDt: DS['default'].attr('date'),
		modifyDt: DS['default'].attr('datetime'),
		prevLogin: DS['default'].attr('date'),
		language: DS['default'].attr('string'),
		hash: DS['default'].attr('string'),
		old_hash: DS['default'].attr('string'),

		version: Ember['default'].computed('modifyDt', function () {
			if (!Ember['default'].isEmpty(this.get('modifyDt'))) {
				return this.get('modifyDt').format('YYMDHms');
			}
			return 1;
		}),
		isAdmin: Ember['default'].computed('scope', function () {
			if (!this.get('scope')) return false;
			return this.get('scope').indexOf("ROLE_ADMIN") >= 0;
		}),
		isCustomer: Ember['default'].computed('scope', function () {
			if (!this.get('scope')) return false;
			return this.get('scope').indexOf("ROLE_CUSTOMER") >= 0;
		}),
		isRegistered: Ember['default'].computed('scope', function () {
			if (!this.get('scope')) return false;
			return this.get('scope').indexOf("ROLE_REGISTERED") >= 0;
		})
	});
	exports['default'] = SessionModel;

});