define('qumla/helpers/group-by', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports.groupBy = groupBy;

	function groupBy(collection, groupField) {
		var result = [];
		collection.forEach(function (stat) {
			var key = stat.get(groupField);
			var item = result.findBy(groupField, key);
			if (!item) {
				item = stat.copy();
				result.pushObject(item);
			}
			item.get('childs').pushObject(stat.copy());
		});
		return result;
	}

	exports['default'] = Ember['default'].Helper.helper(groupBy);

});