define('qumla/services/qstore', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	exports['default'] = DS['default'].Store.extend({
		loader: Ember['default'].inject.service(),
		all: function all(modelName, options) {
			var ready = false;
			var self = this;
			var p = this.findAll(modelName, options);
			p.then(function () {
				ready = true;
			});
			Ember['default'].run.later(function () {
				if (!ready) {
					self.get('loader').startLoadProcess(p);
				}
			}, 400);
			return this.findAll(modelName, options);
		}
	});

});