define('qumla/models/image', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		title: DS['default'].attr('string'),
		path: DS['default'].attr('string'),
		dominant: DS['default'].attr('string'),
		type: DS['default'].attr('number'),
		status: DS['default'].attr('number'),
		tag: DS['default'].attr('string'),
		stringRepr: (function () {
			return this.get('title') + '/' + this.get('path') + "/" + this.get('status');
		}).property('type', 'text'),
		editables: function editables() {
			return ['title', 'dominant', 'status', 'type', 'tag'];
		}
	});
	Ember['default'].Inflector.inflector.uncountable('image');

	exports['default'] = model;

});