define('qumla/pods/components/search-box/component', ['exports', 'ember', 'qumla/app', 'qumla/helpers/to-ascii'], function (exports, Ember, App, to_ascii) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		tagName: "",
		query: "",
		vcallback: function vcallback() {
			var _this = this;

			var self = this;
			if (this.actValue.length >= 2) {
				(function () {
					var queryS = App['default'].getData('/search', true, "POST", true, false, { query: _this.actValue, l: _this.language }, null, null);
					var awesome = _this;
					queryS.then(function (result) {
						awesome._list = [];
						result.forEach(function (item, index) {
							awesome._list.push(item);
						});
						awesome.evaluateResult(awesome.actValue);
					});
				})();
			}
		},
		didInsertElement: function didInsertElement() {
			var self = this;
			var awesome = new Awesomplete($("input.quicksearch")[0], {
				list: [],
				filter: function filter(text, input) {
					return true;
				},
				sort: null,
				item: function item(_item, input) {
					var html = input === '' ? _item.title : _item.title.replace(new RegExp(input.trim().replace(/[-\\^$*+?.()|[\]{}]/g, "\\$&"), "gi"), "<mark>$&</mark>");
					var cla = "s-question";
					if (_item.title.indexOf("#") === 0) {
						cla = "s-tag";
					}
					var id = "";
					if (_item.type === 'q') {
						id = "q-" + _item.id;
					} else {
						id = "t-" + _item.id;
					}
					var element = document.createElement("li");
					element.innerHTML = html;
					element.setAttribute("class", cla);
					element.setAttribute("id", id);
					element.setAttribute("aria-selected", false);
					return element;
				},
				valueCallback: function valueCallback(value) {
					this.actValue = value;
					this.language = self.get('session.language');
					this.comp = self;
					Ember['default'].run.debounce(this, self.vcallback, 300);
				}
			});
			awesome.input.addEventListener('awesomplete-select', function (event) {
				if (event.origin.id.substring(0, 1) === 'q') {
					self.sendAction('jumpToQuestion', to_ascii.toAscii(event.text), event.origin.id.substring(2));
				} else {
					self.sendAction('jumpToTag', event.text);
				}
				return true;
			});
			awesome.input.addEventListener('awesomplete-enter', function (event) {
				self.sendAction('jumpToSearch', self.get('query'));
			});
		}
	});

});