define('qumla/components/info-modal', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		options: {
			dismissible: false, // Modal can be dismissed by clicking outside of the modal
			opacity: 0.5, // Opacity of modal background
			in_duration: 300, // Transition in duration
			out_duration: 300, // Transition out duration
			ready: null, // Callback for Modal open
			complete: null },
		// Callback for Modal close
		didInsertElement: function didInsertElement() {
			this.$("#infoModal").openModal(this.get('options'));
		},
		/*
	 willDestroyElement:function(){
	 	this.$("#infoModal").closeModal();
	 },
	 */
		actions: {
			close: function close() {
				this.$("#infoModal").closeModal(this.get('options'));
				var c = this;
				Ember['default'].run.later(function () {
					c.sendAction('closeModal');
				}, 300);
			}
		}
	});

});