define('qumla/models/country', ['exports', 'ember-data', 'ember'], function (exports, DS, Ember) {

	'use strict';

	var model = DS['default'].Model.extend({
		countryName: DS['default'].attr('string')
	});
	Ember['default'].Inflector.inflector.uncountable('country');
	exports['default'] = model;

});