define('qumla/services/appstate', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Service.extend({
		width: 0,
		height: 0,
		isMobile: Ember['default'].computed('width', function () {
			if (this.get("width") === 0) {
				this.set('width', Ember['default'].$(window).width());
			}
			if (this.get("width") < 703) {
				return true;
			}
			return false;
		})
	});

});