define('qumla/pods/question/result/controller', ['exports', 'ember', 'qumla/mixins/comment-actions', 'qumla/models/subscription', 'qumla/helpers/aggregate-sum', 'qumla/helpers/group-by', 'qumla/app', 'ember-cp-validations'], function (exports, Ember, CommentAction, Subscription, aggregate_sum, group_by, App, ember_cp_validations) {

    'use strict';

    var Validations = ember_cp_validations.buildValidations({
        followaddress: [ember_cp_validations.validator('presence', {
            presence: true,
            message: ' '
        }), ember_cp_validations.validator('format', {
            type: 'email'
        })]
    });
    exports['default'] = Ember['default'].Controller.extend(Validations, CommentAction['default'], {
        optionStatRaw: null, //this is an answer-stat list
        highest: null,
        first: null,
        session: Ember['default'].inject.service(),
        token: Ember['default'].computed.readOnly('session.token'),
        question: null,
        votedOptions: [],

        // there are two data maps on for country and one for detail of clicked country as regionData. The navigation between them hapens from region-map (action up and set zoomCountry) and region maps stored both data
        countryData: null,
        regionData: null,
        zoomCountry: null,
        percentageHistory: true,

        countryList: null,
        historyStatData: Ember['default'].Object.extend({
            xaxis: null,
            series: null
        }).create(),
        followaddress: null,
        answerstathistoryRaw: null,
        historyEnabled: Ember['default'].computed('answerstathistory', function () {
            var answerstathistory = this.get('answerstathistory');
            return answerstathistory.xaxis.length >= 4;
        }),

        summary: Ember['default'].computed('optionStatRaw', function () {
            this.set("first", this.get('optionStatRaw').objectAt(0));
            var aggregatedByOption = aggregate_sum.aggregateSum(this.get('optionStatRaw'), 'option.id', 'count');
            var sortedList = aggregatedByOption.sortBy('count').reverse();
            var votedOptions = [];
            for (var i = 0; i < sortedList.length; i++) {
                sortedList[i].set('option.color', Ember['default'].colorList[i]);
                sortedList[i].set('option.index', i);
                votedOptions.push(sortedList[i].get('option'));
            }
            Ember['default'].Logger.debug(votedOptions);
            this.set('votedOptions', votedOptions);

            if (sortedList.length > 0 && sortedList[0].count > 1) {
                Ember['default'].Logger.debug('top count:' + sortedList[0].count);
                this.set('highest', sortedList[0]);
            } else {
                this.set('highest', null);
            }

            return sortedList;
        }),
        answerstathistory: Ember['default'].computed('answerstathistoryRaw', 'percentageHistory', function () {
            var xaxis = {};
            var options = {};
            var seriesList = this.get('answerstathistoryRaw');
            var self = this;
            seriesList.map(function (item) {
                if (!xaxis[item.date]) {
                    xaxis[item.date] = {};
                }
                var value = item.count;
                if (self.get('percentageHistory')) {
                    value = item.percent;
                }
                xaxis[item.date][item.option] = value;
                options[item.option] = value;
            });
            var optionsArray = [];
            for (var option in options) {
                optionsArray.push({
                    option: option,
                    value: options[option]
                });
            }
            optionsArray = optionsArray.sortBy('value').reverse();
            var series = {};
            optionsArray.map(function (item) {
                series[item.option] = [];
            });

            var l = [];
            for (var x in xaxis) {
                var m = moment(x);
                l.push(m.format('D. MMM'));
                for (var option in options) {
                    series[option].push(xaxis[x][option]);
                }
            }
            var s = [];
            optionsArray.map(function (item) {
                s.push(series[item.option]);
            });
            var o = this.get('historyStatData');
            o.set('xaxis', l);
            o.set('series', s);
            return o;
        }),

        actions: {
            regionClicked: function regionClicked(region) {
                var self = this;
                var locationP = App['default'].getData('/regionresult', true, "GET", true, false, {
                    qid: this.get('question.id'),
                    country: region.region
                }, null, null);
                locationP.then(function (data) {
                    self.set('regionData', data);
                    self.set('zoomCountry', region.region);
                });
            },
            backToRegion: function backToRegion() {
                this.set('regionMapData', this.get('regionDataBackup'));
            },
            openEmailModal: function openEmailModal() {
                $('#modal1').openModal();
            },
            subscribe: function subscribe() {
                var self = this;
                var subscription = this.store.createRecord('subscription', {
                    email: this.get('followaddress'),
                    question: this.get('question.id')
                });
                subscription.save().then(function () {
                    self.set('question.subscribed', true);
                    $('#modal1').closeModal();
                })['catch'](function (status) {
                    console.log('subscription error:' + status);
                });
            },
            onReadyAction: function onReadyAction() {
                Ember['default'].$('ul.tabs').tabs();
                Ember['default'].Logger.debug('tabs are initialized');
            },
            togglePercentage: function togglePercentage() {
                this.set('percentageHistory', !this.get('percentageHistory'));
            }
        }
    });

});