define('qumla/validators/unique-login', ['exports', 'ember', 'ember-cp-validations/validators/base', 'qumla/app'], function (exports, Ember, BaseValidator, App) {

  'use strict';

  exports['default'] = BaseValidator['default'].extend({
    store: Ember['default'].inject.service(),

    validate: function validate(value, options, model, attribute) {
      if (!value) return true;
      var id = model.get('id') || 0;
      return App['default'].getData("/common/check/login?login=" + value + "&eId=" + id, true, "GET", true, false, {}).then(function (result) {
        if (result === 'NULL') {
          return true;
        } else {
          return App['default'].locX("/signup/alreadyexists");
        }
      });
    }
  });

});