define('qumla/helpers/concat-str', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports.concat = concat;

	function concat(params) {
		var r = '';
		for (var i = 0; i < params.length; i++) {
			r += params[i];
		}
		return r;
	}

	exports['default'] = Ember['default'].Helper.helper(concat);

});