define('qumla/pods/components/option-stat/component', ['exports', 'ember', 'qumla/helpers/aggregate-sum'], function (exports, Ember, aggregate_sum) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		input: null,
		highest: null,
		first: null,
		summary: Ember['default'].computed('input', function () {
			this.set("first", this.get('stats').objectAt(0));
			var aggregatedByOption = aggregate_sum.aggregateSum(this.get('input'), 'option.id', 'count');
			var sortedList = aggregatedByOption.sortBy('count').reverse();
			if (sortedList.length > 1 && sortedList[0].count > sortedList[1].count) {
				this.set('highest', sortedList[0]);
			} else {
				this.set('highest', null);
			}
			return sortedList;
		})
	});

});