define('qumla/pods/profile/index/route', ['exports', 'ember', 'qumla/util/q-infinity-route', 'qumla/config/environment'], function (exports, Ember, QInfinityRoute, ENV) {

	'use strict';

	exports['default'] = QInfinityRoute['default'].extend({
		model: function model(params) {
			return this.infinityModel("question", { perPage: ENV['default'].APP.perPage, startingPage: 1, filter: { sessionFilter: true } });
		}
	});

});