define('qumla/mixins/geolocation-aware', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Mixin.create({
		getGeoLocation: function getGeoLocation() {
			// Try W3C Geolocation (Preferred)
			return new Ember['default'].RSVP.Promise(function (resolve, reject) {
				if (navigator.geolocation) {
					navigator.geolocation.getCurrentPosition(function (position) {
						var initialLocation = new google.maps.LatLng(position.coords.latitude, position.coords.longitude);
						resolve(initialLocation);
					}, function () {
						resolve({ browserSupportFlag: true, error: 'unable to getlocation' });
					});
				}
				// Browser doesn't support Geolocation
				else {
						resolve({ browserSupportFlag: false });
					}
			});
		}
	});

});