define('qumla/serializers/user', ['exports', 'ember-data'], function (exports, DS) {

	'use strict';

	exports['default'] = DS['default'].JSONAPISerializer.extend({
		serialize: function serialize(snapshot, options) {
			var o = this._super(snapshot, options);

			if (o.data.attributes) {
				delete o.data.attributes.password2;
			}
			return o;
		}
	});

});