define('qumla/models/question', ['exports', 'ember-data', 'ember', 'qumla/helpers/to-ascii'], function (exports, DS, Ember, to_ascii) {

	'use strict';

	var model = DS['default'].Model.extend({
		title: DS['default'].attr('string'),
		status: DS['default'].attr('number'),
		mandatory: DS['default'].attr('boolean'),
		type: DS['default'].attr('number'), // 1 - yes/no, 
		description: DS['default'].attr('string'),
		encodedDescription: DS['default'].attr('string'),
		url: DS['default'].attr('string'),
		options: DS['default'].hasMany('option'),
		comments: DS['default'].hasMany('comment'),
		img: DS['default'].attr('string'),
		color: DS['default'].attr('string'),
		answered: DS['default'].attr('boolean'),
		validTill: DS['default'].attr('mdate'),
		ticket: DS['default'].attr('string'),
		language: DS['default'].attr('string'),
		subscribed: DS['default'].attr('boolean'),
		answerCount: DS['default'].attr('number'),
		dashboard: DS['default'].attr('number'),
		hasAdvancedDashboard: Ember['default'].computed('dashboard', function () {
			return this.get('dashboard') === 1;
		}),

		headerImg: Ember['default'].computed(function () {
			return '/img/question/' + this.get('img');
		}),
		privateUrl: Ember['default'].computed('ticket', 'id', function () {
			if (this.get('id') && this.get('ticket')) {
				return 'https://qumla.com/question/' + this.get('id') + '/' + to_ascii.toAscii(this.get('title')) + '/detail?t=' + this.get('ticket');
			} else {
				return '';
			}
		}),
		sortedOptions: Ember['default'].computed.sort('options', function (a, b) {
			if (a.get('ord') > b.get('ord')) {
				return 1;
			} else if (a.get('ord') < b.get('ord')) {
				return -1;
			}
			return 0;
		}),
		isClosed: Ember['default'].computed('status', function () {
			return this.get('status') === 50;
		})
	});
	Ember['default'].Inflector.inflector.uncountable('question');

	exports['default'] = model;

});