          var el1 = dom.createElement("div");
          var el2 = dom.createTextNode("We collect very little information that could be used to identify you personally. A non-unique username is randomly assigned to you that you may change at any time. If you change your username, please be sure to create a username that doesnt disclose any personally identifying information. If you decide to invite your friends to use the Services via our invite friends feature, you will also need to reveal your cell phone number to your friend when you send the invitation. No mobile number or email address is ever stored on our servers.");
