          dom.appendChild(el0, el1);
          var el1 = dom.createElement("div");
          var el2 = dom.createTextNode("While youre downloading or using our mobile applications or Services, we may ask for, access, or track location-based information such as device-provided latitude or longitude, if you have location services enabled for the application. One reason for doing this is to help you explore postings in your area or for us to determine how many postings come from a particular area. If you are using an Android device, our Services may continue to run in the background. In this instance, your device may also periodically send us location information. However, giving us permission to access or track any location-based data is absolutely voluntary. In fact, you may freely opt into or opt out of location-based portions of our Services at any time using the app- or device-level settings on iOS or the device-level settings on Android. However, please bear in mind that, even if you have disabled location services, we may still determine your city, state, and country location based on your IP address (but not your exact location).");
