define('qumla/pods/filter/route', ['exports', 'ember', 'qumla/util/q-infinity-route', 'qumla/config/environment'], function (exports, Ember, QInfinityRoute, ENV) {

	'use strict';

	exports['default'] = QInfinityRoute['default'].extend({
		appstate: Ember['default'].inject.service(),
		titleToken: 'Latest questions',
		model: function model(params) {
			var questionPromise;
			if (params.channel === 'latest') {
				questionPromise = this.infinityModel("question", { perPage: ENV['default'].APP.perPage, startingPage: 1, filter: { latest: true } });
			}
			var tags;
			if (!this.get('appstate.isMobile')) {
				tags = this.store.query('tag', { filter: { popular: true } });
			}

			return Ember['default'].RSVP.hash({
				questions: questionPromise,
				tags: tags
			});
		},
		setupController: function setupController(controller, model) {
			controller.set('model', model.questions);
			controller.set('questions', model.questions);
			controller.set('filterName', this.paramsFor('filter').channel);
			controller.set('tags', model.tags);
		}
	});

});