/* jshint ignore:start */

define('qumla/config/environment', ['ember'], function(Ember) {
  var prefix = 'qumla';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("qumla/tests/test-helper");
} else {
  require("qumla/app")["default"].create({"perPage":5,"name":"qumla","version":"0.0.0+abbe8fdf"});
}

/* jshint ignore:end */
