define('qumla/pods/activate/route', ['exports', 'ember', 'qumla/app'], function (exports, Ember, App) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend({
		setupController: function setupController(controller, model) {
			var activate = App['default'].getData('/activate/' + model.code + "/" + model.email, true, 'GET', true, false, {}, null, null);
			activate.then(function () {
				controller.set('activated', true);
			}, function () {
				controller.set('activated', false);
			});
		}
	});

});