define('qumla/pods/components/q-signup/component', ['exports', 'ember', 'qumla/app', 'ember-cp-validations'], function (exports, Ember, App, ember_cp_validations) {

    'use strict';

    var Validations = ember_cp_validations.buildValidations({
        oldPassword: [ember_cp_validations.validator('presence', true), ember_cp_validations.validator('length', {
            min: 4,
            max: 16
        })],
        newPassword: [ember_cp_validations.validator('presence', true), ember_cp_validations.validator('length', {
            min: 4,
            max: 16
        })],
        forgotemail: [ember_cp_validations.validator('presence', {
            presence: true,
            message: ' '
        }), ember_cp_validations.validator('format', {
            type: 'email'
        })],
        newPassword2: [ember_cp_validations.validator('presence', {
            presence: true,
            message: ' '
        }), ember_cp_validations.validator('confirmation', {
            on: 'newPassword',
            description: 'Passwords'
        })]
    });

    exports['default'] = Ember['default'].Component.extend(Validations, {
        user: null,
        oldPassword: null,
        newPassword: null,
        newPassword2: null,
        forgotemail: null,
        privacy: false,
        modify: false,
        saveDisabled: Ember['default'].computed('user.name', 'user.email', 'user.login', 'user.password', 'user.password2', 'privacy', function () {
            if (this.get('modify')) {

                var valid = this.get('user.validations.attrs.name.isValid') || false;
                valid = valid && (this.get('user.validations.attrs.login.isValid') || false);
                return !valid;
            } else {
                return !this.get('user.validations.isValid') || !this.get('privacy');
            }
        }),
        changePasswordEnabled: Ember['default'].computed('user.password', 'modify', function () {
            return this.get('user.password') && this.get('modify');
        }),
        forgotDisabled: Ember['default'].computed('forgotemail', 'newPassword', 'newPassword2', function () {
            var valid = this.get('validations.attrs.newPassword.isValid') && this.get('validations.attrs.newPassword2.isValid') && this.get('validations.attrs.forgotemail.isValid');
            return !valid;
        }),
        title: Ember['default'].computed('midify', function () {
            if (this.get('modify')) {
                return App['default'].locX('/profile/modify');
            } else {
                return App['default'].locX('/profile/signup');
            }
        }),
        actions: {
            sendData: function sendData() {
                var action = this.attrs['save'];
                if (action) {
                    action(this.get('user'));
                }
            },
            sendPassword: function sendPassword() {
                var self = this;
                var action = this.attrs['changePassword'];
                if (action) {
                    action(this.get('oldPassword'), this.get('newPassword'));
                }
            },
            forgotPassword: function forgotPassword() {
                var self = this;
                var action = this.attrs['forgotPassword'];
                if (action) {
                    action(this.get('forgotemail'), this.get('newPassword'));
                }
            },
            privacyCheck: function privacyCheck() {
                this.set('privacy', !this.get('privacy'));
            }

        }
    });

});