define('qumla/pods/components/question-card/component', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Component.extend({
		question: null,
		answerOption: null,
		answerOptionId: null,
		hideHeader: false,

		showDetail: false,
		showOptions: true,

		enableDetailLink: true,
		enableEditLink: false,
		hideQuestionDetail: false,

		showReadMoreButton: Ember['default'].computed('question.description', function () {
			return this.get('question.description').length > 45 && this.get('showDetail');
		}),
		headerImg: Ember['default'].computed.alias('question.headerImg'),
		classNames: ['card', 'question-card'],
		updateAnswerId: function updateAnswerId(aid) {
			this.get('question.options').map(function (data) {
				data.set('answer', aid);
			});
		},
		showDescription: Ember['default'].computed('question.description', function () {
			return this.get('question.description') && !this.get('hideQuestionDetail');
		}),
		showUrl: Ember['default'].computed('question.description', function () {
			return this.get('question.url') && !this.get('hideQuestionDetail');
		}),
		actions: {
			selectAnswer: function selectAnswer(option) {
				this.set('answerOption', option);
				this.set('answerOptionId', option.get('id'));
				this.updateAnswerId(option.get('id'));
			},
			answerChanged: function answerChanged() {
				this.updateAnswerId(this.get('answerOptionId'));
			},
			vote: function vote(option) {
				this.sendAction('onVote', this.get('question'), option);
			},
			toggleDetail: function toggleDetail() {
				this.set('showDetail', !this.get('showDetail'));
			},
			openDetail: function openDetail() {
				this.set('showDetail', true);
			}
		}
	});

});