define('qumla/helpers/display-votes', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports.v = v;

	function v(params) {
		if (params[0] > 1) {
			return '<span class="winner-text">Winner:<br>' + params[0] + '&nbsp;votes</span>';
		} else {
			return 'Need more vote!';
		}
	}

	exports['default'] = Ember['default'].Helper.helper(v);

});